'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Image from 'next/image'
import { createClient } from '@/lib/supabase/client'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Tabs, TabsList, TabsTrigger, TabsContent } from '@/components/ui/tabs'
import {
  Building2,
  Users,
  CheckCircle,
  XCircle,
  Clock,
  Plus,
  Copy,
  Check,
  BarChart3,
  BookOpen,
  LogOut,
  RefreshCw,
  Shield,
  MapPin,
} from 'lucide-react'

export const dynamic = 'force-dynamic'

interface ClassData {
  id: string
  name: string
  description: string
  code: string
  max_teachers: number
  max_students: number
  is_active: boolean
  created_at: string
  teacher_count?: number
  student_count?: number
}

interface TeacherRequest {
  id: string
  classroom_id: string
  teacher_id: string
  status: string
  requested_at: string
  teacher_name?: string
  teacher_email?: string
  classroom_name?: string
}

interface DistrictData {
  id: string
  name: string
  description: string
  code: string
  max_students: number
  max_teachers: number
  is_active: boolean
  created_at: string
  admin_id?: string
  admin_name?: string
  admin_email?: string
  teacher_count?: number
  student_count?: number
}

interface DistrictClassRequest {
  id: string
  district_id: string
  classroom_id: string
  teacher_id: string
  status: string
  requested_at: string
  district_name?: string
  classroom_name?: string
  teacher_name?: string
  teacher_email?: string
}

export default function SuperAdminPanel() {
  const [user, setUser] = useState<any>(null)
  const [classes, setClasses] = useState<ClassData[]>([])
  const [requests, setRequests] = useState<TeacherRequest[]>([])
  const [districts, setDistricts] = useState<DistrictData[]>([])
  const [districtRequests, setDistrictRequests] = useState<DistrictClassRequest[]>([])
  const [stats, setStats] = useState({ classes: 0, activeTeachers: 0, totalStudents: 0, pendingRequests: 0, districts: 0 })
  const [loading, setLoading] = useState(true)
  const [copiedCode, setCopiedCode] = useState<string | null>(null)
  const [showNewClass, setShowNewClass] = useState(false)
  const [showNewDistrict, setShowNewDistrict] = useState(false)
  const [activeTab, setActiveTab] = useState('classes')
  const [newClass, setNewClass] = useState({
    name: '',
    description: '',
    max_teachers: 1,
    max_students: 50,
  })
  const [newDistrict, setNewDistrict] = useState({
    name: '',
    description: '',
    max_students: 500,
    max_teachers: 10,
  })
  const [creating, setCreating] = useState(false)
  const router = useRouter()

  useEffect(() => {
    loadData()
  }, [])

  async function loadData() {
    const supabase = createClient()
    
    try {
      const {
        data: { user: authUser },
        error: userError,
      } = await supabase.auth.getUser()

      if (userError || !authUser) {
        router.push('/auth/login')
        return
      }

      // Check if user is full_admin
      const { data: profile } = await supabase.from('profiles').select('role').eq('id', authUser.id).single()

      if (profile?.role !== 'full_admin') {
        router.push('/protected')
        return
      }

      setUser(authUser)

      // Load classes with counts
      const { data: classesData } = await supabase
        .from('classrooms')
        .select('*')
        .eq('created_by_admin', true)
        .order('created_at', { ascending: false })

      if (classesData) {
        // Get teacher and student counts for each class
        const classesWithCounts = await Promise.all(
          classesData.map(async (cls) => {
            const { data: teacherReqs } = await supabase
              .from('teacher_requests')
              .select('id')
              .eq('classroom_id', cls.id)
              .eq('status', 'approved')

            const { data: enrollments } = await supabase
              .from('enrollments')
              .select('id')
              .eq('classroom_id', cls.id)

            return {
              ...cls,
              teacher_count: teacherReqs?.length || 0,
              student_count: enrollments?.length || 0,
            }
          })
        )

        setClasses(classesWithCounts)
      }

      // Load pending teacher requests
      const { data: requestsData } = await supabase
        .from('teacher_requests')
        .select(
          `
          *,
          profiles!teacher_requests_teacher_id_fkey(full_name, email),
          classrooms(name)
        `
        )
        .eq('status', 'pending')
        .order('requested_at', { ascending: false })

      if (requestsData) {
        const formattedRequests = requestsData.map((req: any) => ({
          id: req.id,
          classroom_id: req.classroom_id,
          teacher_id: req.teacher_id,
          status: req.status,
          requested_at: req.requested_at,
          teacher_name: req.profiles?.full_name,
          teacher_email: req.profiles?.email,
          classroom_name: req.classrooms?.name,
        }))

        setRequests(formattedRequests)
      }

      // Load all districts
      const { data: districtsData } = await supabase
        .from('districts')
        .select('*, profiles!districts_admin_id_fkey(full_name, email)')
        .order('created_at', { ascending: false })

      if (districtsData) {
        // Get teacher and student counts for each district
        const districtsWithCounts = await Promise.all(
          districtsData.map(async (dist: any) => {
            const { data: teachers } = await supabase
              .from('district_teachers')
              .select('id')
              .eq('district_id', dist.id)

            const { data: students } = await supabase
              .from('district_students')
              .select('id')
              .eq('district_id', dist.id)

            return {
              ...dist,
              admin_name: dist.profiles?.full_name,
              admin_email: dist.profiles?.email,
              teacher_count: teachers?.length || 0,
              student_count: students?.length || 0,
            }
          })
        )

        setDistricts(districtsWithCounts)
      }

      // Load pending district class requests
      const { data: districtRequestsData } = await supabase
        .from('class_requests')
        .select(
          `
          *,
          districts(name),
          classrooms(name),
          profiles!class_requests_teacher_id_fkey(full_name, email)
        `
        )
        .eq('status', 'pending')
        .order('requested_at', { ascending: false })

      if (districtRequestsData) {
        const formattedDistrictRequests = districtRequestsData.map((req: any) => ({
          id: req.id,
          district_id: req.district_id,
          classroom_id: req.classroom_id,
          teacher_id: req.teacher_id,
          status: req.status,
          requested_at: req.requested_at,
          district_name: req.districts?.name,
          classroom_name: req.classrooms?.name,
          teacher_name: req.profiles?.full_name,
          teacher_email: req.profiles?.email,
        }))

        setDistrictRequests(formattedDistrictRequests)
      }

      // Calculate stats
      const totalActiveTeachers = await supabase
        .from('teacher_requests')
        .select('teacher_id', { count: 'exact' })
        .eq('status', 'approved')

      const totalStudents = await supabase.from('enrollments').select('id', { count: 'exact' })

      setStats({
        classes: classesData?.length || 0,
        activeTeachers: totalActiveTeachers.count || 0,
        totalStudents: totalStudents.count || 0,
        pendingRequests: (requestsData?.length || 0) + (districtRequestsData?.length || 0),
        districts: districtsData?.length || 0,
      })
    } catch (err: any) {
      console.error('[v0] Error loading admin data:', err)
    } finally {
      setLoading(false)
    }
  }

  async function handleCreateClass() {
    if (!newClass.name.trim()) return

    setCreating(true)
    const supabase = createClient()

    try {
      // Generate unique class code
      const code = Math.random().toString(36).substring(2, 10).toUpperCase()

      const { data, error } = await supabase
        .from('classrooms')
        .insert({
          name: newClass.name,
          description: newClass.description,
          code,
          teacher_id: user.id,
          created_by_admin: true,
          is_active: true,
          max_teachers: newClass.max_teachers,
          max_students: newClass.max_students,
        })
        .select()

      if (error) throw error

      // Log activity
      await supabase.from('admin_activity_log').insert({
        admin_id: user.id,
        action: 'create_class',
        target_id: data[0].id,
        details: { class_name: newClass.name, code },
      })

      setShowNewClass(false)
      setNewClass({ name: '', description: '', max_teachers: 1, max_students: 50 })
      loadData()
    } catch (err: any) {
      alert('Error creating class: ' + (err.message || 'Unknown error'))
    } finally {
      setCreating(false)
    }
  }

  async function handleApproveRequest(requestId: string, classroomId: string, teacherId: string) {
    const supabase = createClient()

    try {
      // Update request status
      await supabase.from('teacher_requests').update({ status: 'approved', reviewed_at: new Date().toISOString(), reviewed_by: user.id }).eq('id', requestId)

      // Update classroom teacher_id
      await supabase.from('classrooms').update({ teacher_id: teacherId }).eq('id', classroomId)

      // Log activity
      await supabase.from('admin_activity_log').insert({
        admin_id: user.id,
        action: 'approve_teacher',
        target_id: requestId,
        details: { teacher_id: teacherId, classroom_id: classroomId },
      })

      loadData()
    } catch (err: any) {
      alert('Error approving request: ' + (err.message || 'Unknown error'))
    }
  }

  async function handleRejectRequest(requestId: string) {
    const supabase = createClient()

    try {
      await supabase.from('teacher_requests').update({ status: 'rejected', reviewed_at: new Date().toISOString(), reviewed_by: user.id }).eq('id', requestId)

      // Log activity
      await supabase.from('admin_activity_log').insert({
        admin_id: user.id,
        action: 'reject_teacher',
        target_id: requestId,
      })

      loadData()
    } catch (err: any) {
      alert('Error rejecting request: ' + (err.message || 'Unknown error'))
    }
  }

  async function handleCreateDistrict() {
    if (!newDistrict.name.trim()) return

    setCreating(true)
    const supabase = createClient()

    try {
      // Generate unique district code
      const code = Math.random().toString(36).substring(2, 10).toUpperCase()

      const { data, error } = await supabase
        .from('districts')
        .insert({
          name: newDistrict.name,
          description: newDistrict.description,
          code,
          admin_id: user.id,
          is_active: true,
          max_teachers: newDistrict.max_teachers,
          max_students: newDistrict.max_students,
        })
        .select()

      if (error) throw error

      // Log activity
      await supabase.from('admin_activity_log').insert({
        admin_id: user.id,
        action: 'create_district',
        target_id: data[0].id,
        details: { district_name: newDistrict.name, code },
      })

      setShowNewDistrict(false)
      setNewDistrict({ name: '', description: '', max_teachers: 10, max_students: 500 })
      loadData()
    } catch (err: any) {
      alert('Error creating district: ' + (err.message || 'Unknown error'))
    } finally {
      setCreating(false)
    }
  }

  async function handleApproveDistrictRequest(requestId: string, districtId: string, classroomId: string) {
    const supabase = createClient()

    try {
      // Update request status
      await supabase
        .from('class_requests')
        .update({
          status: 'approved',
          reviewed_at: new Date().toISOString(),
          reviewed_by: user.id
        })
        .eq('id', requestId)

      // Add classroom to district
      await supabase.from('district_classrooms').insert({
        district_id: districtId,
        classroom_id: classroomId,
      })

      // Log activity
      await supabase.from('admin_activity_log').insert({
        admin_id: user.id,
        action: 'approve_district_request',
        target_id: requestId,
        details: { district_id: districtId, classroom_id: classroomId },
      })

      loadData()
    } catch (err: any) {
      alert('Error approving district request: ' + (err.message || 'Unknown error'))
    }
  }

  async function handleRejectDistrictRequest(requestId: string) {
    const supabase = createClient()

    try {
      await supabase
        .from('class_requests')
        .update({
          status: 'rejected',
          reviewed_at: new Date().toISOString(),
          reviewed_by: user.id
        })
        .eq('id', requestId)

      // Log activity
      await supabase.from('admin_activity_log').insert({
        admin_id: user.id,
        action: 'reject_district_request',
        target_id: requestId,
      })

      loadData()
    } catch (err: any) {
      alert('Error rejecting district request: ' + (err.message || 'Unknown error'))
    }
  }

  function copyCode(code: string) {
    navigator.clipboard.writeText(code)
    setCopiedCode(code)
    setTimeout(() => setCopiedCode(null), 2000)
  }

  async function handleSignOut() {
    const supabase = createClient()
    await supabase.auth.signOut()
    router.push('/auth/login')
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950">
        <div className="flex flex-col items-center gap-6">
          <div className="relative">
            <div className="w-16 h-16 border-4 border-purple-500/30 rounded-full"></div>
            <div className="w-16 h-16 border-4 border-purple-500 border-t-transparent rounded-full animate-spin absolute top-0"></div>
          </div>
          <p className="text-lg text-slate-300 font-medium">Loading Admin Panel...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950 font-sans">
      {/* Header */}
      <header className="border-b border-white/10 bg-slate-900/80 backdrop-blur-xl sticky top-0 z-50 shadow-xl shadow-black/20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center gap-4">
              <div className="relative w-12 h-12 flex-shrink-0 rounded-xl bg-gradient-to-br from-violet-500 via-purple-500 to-fuchsia-500 flex items-center justify-center shadow-lg shadow-purple-500/30 hover:shadow-purple-500/50 transition-all duration-300 hover:scale-105">
                <Shield className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1 className="text-lg font-bold text-white leading-tight tracking-tight">Super Admin</h1>
                <p className="text-sm text-slate-400 font-medium">Control Panel</p>
              </div>
            </div>
            <Button variant="ghost" onClick={handleSignOut} className="gap-2 text-slate-300 hover:text-white hover:bg-white/10 font-medium">
              <LogOut className="w-4 h-4" />
              <span className="hidden sm:inline">Sign Out</span>
            </Button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-6 mb-10">
          <Card className="bg-gradient-to-br from-cyan-500/20 to-blue-500/20 border border-cyan-400/30 shadow-lg shadow-cyan-500/10 hover:shadow-cyan-500/20 transition-all hover:scale-105">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="p-2 bg-cyan-500/20 rounded-lg">
                  <Building2 className="w-5 h-5 text-cyan-300" />
                </div>
                <span className="text-4xl font-bold text-white tracking-tight">{stats.classes}</span>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm font-semibold text-slate-300">Total Classes</p>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-purple-500/20 to-violet-500/20 border border-purple-400/30 shadow-lg shadow-purple-500/10 hover:shadow-purple-500/20 transition-all hover:scale-105">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="p-2 bg-purple-500/20 rounded-lg">
                  <MapPin className="w-5 h-5 text-purple-300" />
                </div>
                <span className="text-4xl font-bold text-white tracking-tight">{stats.districts}</span>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm font-semibold text-slate-300">Districts</p>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-green-500/20 to-emerald-500/20 border border-green-400/30 shadow-lg shadow-green-500/10 hover:shadow-green-500/20 transition-all hover:scale-105">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="p-2 bg-green-500/20 rounded-lg">
                  <Users className="w-5 h-5 text-green-300" />
                </div>
                <span className="text-4xl font-bold text-white tracking-tight">{stats.activeTeachers}</span>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm font-semibold text-slate-300">Active Teachers</p>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-blue-500/20 to-indigo-500/20 border border-blue-400/30 shadow-lg shadow-blue-500/10 hover:shadow-blue-500/20 transition-all hover:scale-105">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="p-2 bg-blue-500/20 rounded-lg">
                  <BookOpen className="w-5 h-5 text-blue-300" />
                </div>
                <span className="text-4xl font-bold text-white tracking-tight">{stats.totalStudents}</span>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm font-semibold text-slate-300">Total Students</p>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-orange-500/20 to-red-500/20 border border-orange-400/30 shadow-lg shadow-orange-500/10 hover:shadow-orange-500/20 transition-all hover:scale-105">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <div className="p-2 bg-orange-500/20 rounded-lg">
                  <Clock className="w-5 h-5 text-orange-300" />
                </div>
                <span className="text-4xl font-bold text-white tracking-tight">{stats.pendingRequests}</span>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm font-semibold text-slate-300">Pending Requests</p>
            </CardContent>
          </Card>
        </div>

        {/* Tabs Navigation */}
        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
          <TabsList className="grid w-full grid-cols-3 mb-10 bg-slate-800/50 p-2 rounded-xl border border-slate-700/50 shadow-lg">
            <TabsTrigger
              value="classes"
              className="gap-2 data-[state=active]:bg-gradient-to-r data-[state=active]:from-cyan-500 data-[state=active]:to-blue-500 data-[state=active]:text-white data-[state=active]:shadow-lg font-semibold transition-all"
            >
              <Building2 className="w-4 h-4" />
              Classes
            </TabsTrigger>
            <TabsTrigger
              value="districts"
              className="gap-2 data-[state=active]:bg-gradient-to-r data-[state=active]:from-purple-500 data-[state=active]:to-violet-500 data-[state=active]:text-white data-[state=active]:shadow-lg font-semibold transition-all"
            >
              <MapPin className="w-4 h-4" />
              Districts
            </TabsTrigger>
            <TabsTrigger
              value="requests"
              className="gap-2 data-[state=active]:bg-gradient-to-r data-[state=active]:from-orange-500 data-[state=active]:to-red-500 data-[state=active]:text-white data-[state=active]:shadow-lg font-semibold transition-all"
            >
              <Clock className="w-4 h-4" />
              Requests
              {stats.pendingRequests > 0 && (
                <span className="ml-1 px-2 py-0.5 text-xs font-bold bg-white/20 text-white rounded-full animate-pulse">
                  {stats.pendingRequests}
                </span>
              )}
            </TabsTrigger>
          </TabsList>

          {/* Classes Tab */}
          <TabsContent value="classes">
            <Card className="border-2">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="text-primary">Class Codes Management</CardTitle>
                    <CardDescription>Create and manage class codes for sale</CardDescription>
                  </div>
                  <Button onClick={() => setShowNewClass(!showNewClass)} className="gap-2 bg-primary hover:bg-primary/90">
                    <Plus className="w-4 h-4" />
                    Create New Class
                  </Button>
                </div>
              </CardHeader>
              <CardContent>
                {showNewClass && (
                  <div className="mb-6 p-6 border-2 border-primary/20 rounded-lg bg-primary/5">
                    <h3 className="font-semibold text-foreground mb-4">New Class Details</h3>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="md:col-span-2">
                        <Label htmlFor="name">Class Name</Label>
                        <Input
                          id="name"
                          placeholder="e.g., Python for Beginners"
                          value={newClass.name}
                          onChange={(e) => setNewClass({ ...newClass, name: e.target.value })}
                          className="mt-1"
                        />
                      </div>
                      <div className="md:col-span-2">
                        <Label htmlFor="description">Description</Label>
                        <Input
                          id="description"
                          placeholder="Course description..."
                          value={newClass.description}
                          onChange={(e) => setNewClass({ ...newClass, description: e.target.value })}
                          className="mt-1"
                        />
                      </div>
                      <div>
                        <Label htmlFor="maxTeachers">Max Teachers</Label>
                        <Input
                          id="maxTeachers"
                          type="number"
                          min="1"
                          value={newClass.max_teachers}
                          onChange={(e) => setNewClass({ ...newClass, max_teachers: parseInt(e.target.value) })}
                          className="mt-1"
                        />
                      </div>
                      <div>
                        <Label htmlFor="maxStudents">Max Students</Label>
                        <Input
                          id="maxStudents"
                          type="number"
                          min="1"
                          value={newClass.max_students}
                          onChange={(e) => setNewClass({ ...newClass, max_students: parseInt(e.target.value) })}
                          className="mt-1"
                        />
                      </div>
                    </div>
                    <div className="flex gap-2 mt-4">
                      <Button onClick={handleCreateClass} disabled={creating || !newClass.name.trim()} className="bg-primary hover:bg-primary/90">
                        {creating ? 'Creating...' : 'Create Class'}
                      </Button>
                      <Button variant="outline" onClick={() => setShowNewClass(false)}>
                        Cancel
                      </Button>
                    </div>
                  </div>
                )}

                <div className="space-y-3">
                  {classes.length === 0 ? (
                    <div className="text-center py-12">
                      <Building2 className="w-12 h-12 text-muted-foreground mx-auto mb-4" />
                      <p className="text-muted-foreground">No classes created yet. Create your first class to get started.</p>
                    </div>
                  ) : (
                    classes.map((cls) => (
                      <div key={cls.id} className="p-4 border-2 border-border rounded-lg bg-card hover:border-primary/50 transition-all">
                        <div className="flex items-start justify-between">
                          <div className="flex-1">
                            <div className="flex items-center gap-3 mb-2">
                              <h3 className="font-semibold text-lg text-foreground">{cls.name}</h3>
                              {cls.is_active ? (
                                <span className="px-2 py-1 text-xs font-medium bg-green-100 text-green-700 rounded-full dark:bg-green-950 dark:text-green-400">
                                  Active
                                </span>
                              ) : (
                                <span className="px-2 py-1 text-xs font-medium bg-gray-100 text-gray-700 rounded-full dark:bg-gray-800 dark:text-gray-400">
                                  Inactive
                                </span>
                              )}
                            </div>
                            {cls.description && <p className="text-sm text-muted-foreground mb-3">{cls.description}</p>}
                            <div className="flex items-center gap-4 text-sm text-muted-foreground">
                              <span className="flex items-center gap-1">
                                <Users className="w-4 h-4" />
                                {cls.teacher_count}/{cls.max_teachers} Teachers
                              </span>
                              <span className="flex items-center gap-1">
                                <BookOpen className="w-4 h-4" />
                                {cls.student_count}/{cls.max_students} Students
                              </span>
                            </div>
                          </div>
                          <div className="flex flex-col items-end gap-2">
                            <div className="flex items-center gap-2">
                              <code className="px-3 py-2 bg-primary/10 border-2 border-primary/20 rounded-lg font-mono text-sm font-semibold text-primary">
                                {cls.code}
                              </code>
                              <Button
                                size="sm"
                                variant="outline"
                                onClick={() => copyCode(cls.code)}
                                className="gap-2"
                              >
                                {copiedCode === cls.code ? <Check className="w-4 h-4 text-green-600" /> : <Copy className="w-4 h-4" />}
                                {copiedCode === cls.code ? 'Copied!' : 'Copy'}
                              </Button>
                            </div>
                            <p className="text-xs text-muted-foreground">
                              Created {new Date(cls.created_at).toLocaleDateString()}
                            </p>
                          </div>
                        </div>
                      </div>
                    ))
                  )}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Districts Tab */}
          <TabsContent value="districts">
            <Card className="border-2">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="text-primary">Districts Management</CardTitle>
                    <CardDescription>Create and manage educational districts</CardDescription>
                  </div>
                  <Button onClick={() => setShowNewDistrict(!showNewDistrict)} className="gap-2 bg-primary hover:bg-primary/90">
                    <Plus className="w-4 h-4" />
                    Create New District
                  </Button>
                </div>
              </CardHeader>
              <CardContent>
                {showNewDistrict && (
                  <div className="mb-6 p-6 border-2 border-primary/20 rounded-lg bg-primary/5">
                    <h3 className="font-semibold text-foreground mb-4">New District Details</h3>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="md:col-span-2">
                        <Label htmlFor="districtName">District Name</Label>
                        <Input
                          id="districtName"
                          placeholder="e.g., Springfield School District"
                          value={newDistrict.name}
                          onChange={(e) => setNewDistrict({ ...newDistrict, name: e.target.value })}
                          className="mt-1"
                        />
                      </div>
                      <div className="md:col-span-2">
                        <Label htmlFor="districtDescription">Description</Label>
                        <Input
                          id="districtDescription"
                          placeholder="District description..."
                          value={newDistrict.description}
                          onChange={(e) => setNewDistrict({ ...newDistrict, description: e.target.value })}
                          className="mt-1"
                        />
                      </div>
                      <div>
                        <Label htmlFor="districtMaxTeachers">Max Teachers</Label>
                        <Input
                          id="districtMaxTeachers"
                          type="number"
                          min="1"
                          value={newDistrict.max_teachers}
                          onChange={(e) => setNewDistrict({ ...newDistrict, max_teachers: parseInt(e.target.value) })}
                          className="mt-1"
                        />
                      </div>
                      <div>
                        <Label htmlFor="districtMaxStudents">Max Students</Label>
                        <Input
                          id="districtMaxStudents"
                          type="number"
                          min="1"
                          value={newDistrict.max_students}
                          onChange={(e) => setNewDistrict({ ...newDistrict, max_students: parseInt(e.target.value) })}
                          className="mt-1"
                        />
                      </div>
                    </div>
                    <div className="flex gap-2 mt-4">
                      <Button onClick={handleCreateDistrict} disabled={creating || !newDistrict.name.trim()} className="bg-primary hover:bg-primary/90">
                        {creating ? 'Creating...' : 'Create District'}
                      </Button>
                      <Button variant="outline" onClick={() => setShowNewDistrict(false)}>
                        Cancel
                      </Button>
                    </div>
                  </div>
                )}

                <div className="space-y-3">
                  {districts.length === 0 ? (
                    <div className="text-center py-12">
                      <MapPin className="w-12 h-12 text-muted-foreground mx-auto mb-4" />
                      <p className="text-muted-foreground">No districts created yet. Create your first district to get started.</p>
                    </div>
                  ) : (
                    districts.map((district) => (
                      <div key={district.id} className="p-4 border-2 border-border rounded-lg bg-card hover:border-primary/50 transition-all">
                        <div className="flex items-start justify-between">
                          <div className="flex-1">
                            <div className="flex items-center gap-3 mb-2">
                              <MapPin className="w-5 h-5 text-primary" />
                              <h3 className="font-semibold text-lg text-foreground">{district.name}</h3>
                              {district.is_active ? (
                                <span className="px-2 py-1 text-xs font-medium bg-green-100 text-green-700 rounded-full dark:bg-green-950 dark:text-green-400">
                                  Active
                                </span>
                              ) : (
                                <span className="px-2 py-1 text-xs font-medium bg-gray-100 text-gray-700 rounded-full dark:bg-gray-800 dark:text-gray-400">
                                  Inactive
                                </span>
                              )}
                            </div>
                            {district.description && <p className="text-sm text-muted-foreground mb-3">{district.description}</p>}
                            <div className="flex items-center gap-4 text-sm text-muted-foreground mb-2">
                              <span className="flex items-center gap-1">
                                <Users className="w-4 h-4" />
                                {district.teacher_count}/{district.max_teachers} Teachers
                              </span>
                              <span className="flex items-center gap-1">
                                <BookOpen className="w-4 h-4" />
                                {district.student_count}/{district.max_students} Students
                              </span>
                            </div>
                            {district.admin_name && (
                              <div className="text-sm text-muted-foreground">
                                <span className="font-medium">Admin:</span> {district.admin_name} ({district.admin_email})
                              </div>
                            )}
                          </div>
                          <div className="flex flex-col items-end gap-2">
                            <div className="flex items-center gap-2">
                              <code className="px-3 py-2 bg-primary/10 border-2 border-primary/20 rounded-lg font-mono text-sm font-semibold text-primary">
                                {district.code}
                              </code>
                              <Button
                                size="sm"
                                variant="outline"
                                onClick={() => copyCode(district.code)}
                                className="gap-2"
                              >
                                {copiedCode === district.code ? <Check className="w-4 h-4 text-green-600" /> : <Copy className="w-4 h-4" />}
                                {copiedCode === district.code ? 'Copied!' : 'Copy'}
                              </Button>
                            </div>
                            <p className="text-xs text-muted-foreground">
                              Created {new Date(district.created_at).toLocaleDateString()}
                            </p>
                          </div>
                        </div>
                      </div>
                    ))
                  )}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Requests Tab */}
          <TabsContent value="requests">
            <div className="space-y-6">
              {/* Teacher Requests */}
              {requests.length > 0 && (
                <Card className="border-2">
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <div>
                        <CardTitle className="text-primary">Pending Teacher Requests</CardTitle>
                        <CardDescription>Review and approve teachers joining classes</CardDescription>
                      </div>
                      <Button size="sm" variant="outline" onClick={loadData} className="gap-2 bg-transparent">
                        <RefreshCw className="w-4 h-4" />
                        Refresh
                      </Button>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-3">
                      {requests.map((request) => (
                        <div key={request.id} className="flex items-center justify-between p-4 border-2 border-border rounded-lg bg-card hover:bg-accent/5 transition-colors">
                          <div className="flex-1">
                            <div className="flex items-center gap-3">
                              <div className="w-10 h-10 rounded-full bg-primary/10 flex items-center justify-center">
                                <Users className="w-5 h-5 text-primary" />
                              </div>
                              <div>
                                <p className="font-semibold text-foreground">{request.teacher_name || 'Unknown Teacher'}</p>
                                <p className="text-sm text-muted-foreground">{request.teacher_email}</p>
                              </div>
                            </div>
                            <div className="mt-2 ml-13">
                              <p className="text-sm text-muted-foreground">
                                Requesting access to: <span className="font-medium text-foreground">{request.classroom_name}</span>
                              </p>
                              <p className="text-xs text-muted-foreground mt-1">
                                {new Date(request.requested_at).toLocaleDateString('en-US', {
                                  month: 'short',
                                  day: 'numeric',
                                  year: 'numeric',
                                  hour: '2-digit',
                                  minute: '2-digit',
                                })}
                              </p>
                            </div>
                          </div>
                          <div className="flex items-center gap-2">
                            <Button
                              size="sm"
                              onClick={() => handleApproveRequest(request.id, request.classroom_id, request.teacher_id)}
                              className="bg-green-600 hover:bg-green-700 text-white gap-2"
                            >
                              <CheckCircle className="w-4 h-4" />
                              Approve
                            </Button>
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => handleRejectRequest(request.id)}
                              className="border-red-200 text-red-600 hover:bg-red-50 dark:hover:bg-red-950/20 gap-2"
                            >
                              <XCircle className="w-4 h-4" />
                              Reject
                            </Button>
                          </div>
                        </div>
                      ))}
                    </div>
                  </CardContent>
                </Card>
              )}

              {/* District Class Requests */}
              {districtRequests.length > 0 && (
                <Card className="border-2">
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <div>
                        <CardTitle className="text-primary">Pending District Class Requests</CardTitle>
                        <CardDescription>Review and approve classes joining districts</CardDescription>
                      </div>
                      <Button size="sm" variant="outline" onClick={loadData} className="gap-2 bg-transparent">
                        <RefreshCw className="w-4 h-4" />
                        Refresh
                      </Button>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-3">
                      {districtRequests.map((request) => (
                        <div key={request.id} className="flex items-center justify-between p-4 border-2 border-border rounded-lg bg-card hover:bg-accent/5 transition-colors">
                          <div className="flex-1">
                            <div className="flex items-center gap-3">
                              <div className="w-10 h-10 rounded-full bg-purple-500/10 flex items-center justify-center">
                                <MapPin className="w-5 h-5 text-purple-500" />
                              </div>
                              <div>
                                <p className="font-semibold text-foreground">{request.teacher_name || 'Unknown Teacher'}</p>
                                <p className="text-sm text-muted-foreground">{request.teacher_email}</p>
                              </div>
                            </div>
                            <div className="mt-2 ml-13">
                              <p className="text-sm text-muted-foreground">
                                Class <span className="font-medium text-foreground">{request.classroom_name}</span> requesting to join{' '}
                                <span className="font-medium text-foreground">{request.district_name}</span>
                              </p>
                              <p className="text-xs text-muted-foreground mt-1">
                                {new Date(request.requested_at).toLocaleDateString('en-US', {
                                  month: 'short',
                                  day: 'numeric',
                                  year: 'numeric',
                                  hour: '2-digit',
                                  minute: '2-digit',
                                })}
                              </p>
                            </div>
                          </div>
                          <div className="flex items-center gap-2">
                            <Button
                              size="sm"
                              onClick={() => handleApproveDistrictRequest(request.id, request.district_id, request.classroom_id)}
                              className="bg-green-600 hover:bg-green-700 text-white gap-2"
                            >
                              <CheckCircle className="w-4 h-4" />
                              Approve
                            </Button>
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => handleRejectDistrictRequest(request.id)}
                              className="border-red-200 text-red-600 hover:bg-red-50 dark:hover:bg-red-950/20 gap-2"
                            >
                              <XCircle className="w-4 h-4" />
                              Reject
                            </Button>
                          </div>
                        </div>
                      ))}
                    </div>
                  </CardContent>
                </Card>
              )}

              {/* No Requests Message */}
              {requests.length === 0 && districtRequests.length === 0 && (
                <Card className="border-2">
                  <CardContent className="py-12">
                    <div className="text-center">
                      <CheckCircle className="w-12 h-12 text-green-500 mx-auto mb-4" />
                      <p className="text-muted-foreground">No pending requests at this time.</p>
                    </div>
                  </CardContent>
                </Card>
              )}
            </div>
          </TabsContent>
        </Tabs>
      </main>
    </div>
  )
}
